* {
  margin: 0;
  padding: 0;
  box-sizing:border-box;
}

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.contact-form-intro {
  font-family: 'Alice', serif;
  font-size: 1.5em;
  color: #523524;
  margin: .75em 0;
}

.required {
  font-family: 'Iceberg', cursive;
  font-size: 1em;
  padding-top: 1em;
  display: block;
  text-align: center;
}

.form-container {
  width: 100vw;
  height: fit-content;
  padding-top: 3em;
  background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #c4d8d6;
  align-items: center;
  justify-content: center;
}

#contact-form {
  margin-left: auto;
  margin-right: auto;
  padding: 2em;
  width: max-content;
  border: solid 1pt #523524;
  border-radius: 5px;
  text-align: left;
  margin-bottom: 5em;
  display: block;
  /* background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
   */
  /* background-color:#c4d8d6; */
}

fieldset {
  border: none;
}

label {
  font-family: 'Iceberg', cursive;
  padding: .5em;
  display: block;
  color: #523524;
}

#inputFirstName, #inputLastName {
  padding-left: 1em;
  height: 2em;
  width: 15em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputEmail {
  padding-left: 1em;
  height: 2em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputMessage {
  padding-left: 1em;
  text-justify: initial;
  resize: none;
  height: 15em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#fs-button {
  text-align: center;
}

.con-response {
  font-family: 'Iceberg', cursive;
  font-size: 1.1em;
  color: #c7742b;
}

button {
  padding: .5em 1.5em;
  font-family: 'Iceberg', cursive;
  font-size: 1.25em;
  color: #eff4f7;
  background-color: #523524;
  border-radius: 10px;
}

.content-section {
  margin-bottom: 50px;
  flex: 1 0 auto;
}

.Footer {
  margin-top: -70px;
  height: 70px;
  background-color: #EFF4F7;
  display: flex;
  flex-shrink: 0;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.footer-text {
  font-family: 'Alice', serif;
  font-size: small;

}


@media only screen and (max-width: 650px) {

  .contact-form-intro {
    font-size: .8em;
  }

  #contact-form {
    padding: 1em;
  }

  #inputEmail,
  #inputMessage {
    width: 20em;
  }

  #inputMessage {
    height: 8em;
  }

  button {
    font-size: .8em;
    padding: .3em .8em;
  }

  .con-response {
    font-size: .75em;
  }

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css2?family=Alice&family=Iceberg&family=Raleway:ital,wght@0,400;1,500&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap');