.projects {
    display: flex;
    flex-direction: column;
    width: 100vw;
    justify-content: center;
    align-items: center;
}

.projects__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80vw;
    margin-bottom: 3em;
}


.projects__nav {
    margin: 2em 0;
    display: flex;
}


.projects__image {
    height: 23rem;
    width: auto;
    margin: 1em 2em;
    border: .5px #C7742B solid;
    transition: .5s ease;
    opacity: 1;
    backface-visibility: hidden;

}

.projects__image-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 75%;
    left: 25%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.projects__nav:hover .projects__image {
    opacity: 0.3;
}

.projects__nav:hover .projects__image-middle {
    opacity: 1;
}

.projects__image-middle-text {
    color: #523524;
    font-family: 'Alice', serif;
    padding: 16px 32px;
}

.projects__copy {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 2em 0;
}

.projects__name {
    color: #523524;
    font-family: 'Iceberg', cursive;
    font-size: 2em;
}

.projects__desc {
    color: #523524;
    font-family: 'Alice', serif;
    margin: 0 1em;
    text-align: justify;
}

button {
    font-size: 1em;
    margin-top: 1.5em;
    width: 7.5em;
    height: 3.75em;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}

/* inside popups */

.content__label-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content-projects {
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.content__key,
.content__value {
    margin: 1em;
}

.popup-content {
    background-color: #523524 !important;
    position: absolute !important;
    border-radius: 20px !important;
    width: 82% !important;
    padding: 2em !important;
    height: 85% !important;
    left: 7% !important;
    right: 50px !important;
    overflow: scroll !important;
    text-align: center !important;
  }

  .close,
  .close:hover,
  .close:active {
    
    cursor: pointer;
    font-family: inherit;
    color:#EFF4F7;
    font-size: 2em;
    font-weight: 800;
  }



@media only screen and (max-width: 650px) {
    .projects__wrapper {
        flex-direction: column;
    }

    .projects__nav {
        margin: .75em 0;
    }

    .projects__image {
        height: 8rem;
        width: auto;
        margin: .5em 1em;
    }

    .projects__copy {
        padding: 1em 0;
    }
    
    .projects__name {
        font-size: .75em;
    }
    
    .projects__desc {
        text-align: left;
        font-size: smaller;
    }
    
    button {
        font-size: .6em;
        margin-top: .85em;
        width: 4.25em;
        height: 3em;
        padding: .25em;
    }

    
    .content__key,
    .content__value {
        margin: .75em;
        font-size: .75em;     
    }
    
    .popup-content {
        background-color: #523524 !important;
        position: absolute !important;
        border-radius: 20px !important;
        width: auto !important;
        padding: 1em !important;
        height: 50% !important;
        left: 6vw !important;
        right: 6vw !important;
        overflow: scroll !important;
        text-align: center !important;
      }
    
      .close,
      .close:hover,
      .close:active {
        
        cursor: pointer;
        font-family: inherit;
        color:#EFF4F7;
        font-size: 2em;
        font-weight: 800;
      }

}

