.navbar {
    margin-top: 1rem;
    height: 3.5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #523524;
}


.navbar__link {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    text-decoration: none;
    font-size: 1.25rem;
}

.navbar__link:hover {
    transform: translateY(3px);
    font-size: 1.35em;
}

@media only screen and (max-width: 480px) {
    .navbar {
        height: 2.5em;
    }
    
    .navbar__link {
        margin: 0 .25em;
        font-size: .8em;
    }
}