p {
    margin: 2em 0;
    text-align: center;
    color: #523524;
    font-size: 1.2rem;
    align-self: center;
}

.title {
    display: flex;
    height: 8em;
    justify-content: center;
    align-items: center;

}

.title__descriptors {
    font-family: 'Iceberg', cursive;
    font-size: 2em;
    color: #523524;

}

.title__descriptors:hover {
    transform: scale(1.5);
    color: #54405B;

}

.material-icons {
    color: #C7742B;
    padding: 0 5px;
}

.material-icons:hover {
    transform: scale(2);
    color: #54405B;
}

.icon-carousel {
    height: 24rem;
    background-color: #C4D8D6;
    padding: 50px 85px;
    text-decoration: none;
    border: none;
}

.carousel-slides {
    margin-top: 2em;
    height: 12.5rem;
    text-decoration: none;
    border: none;
} 

.carousel-images {
    height: 150px;
    width: auto;
    text-decoration: none;
    border: none;
}

.carousel-images:hover {
    height: 160px;
    text-decoration: none;
    border: none;
}

.about-me__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-me__img {
    height: 30em;
    width: auto;
}

.about-me {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 30em;
    background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}


.about-me__copy {
    font-family: 'Alice', serif;
    font-size: 1.5em;
    color: #523524;
    margin: 1em;
    text-shadow: 2px 2px 2px #FFFFFF;
}

@media only screen and (max-width: 650px) {
    .title {
        height: 2em;
        margin: 1em;
    }

    .title__descriptors {
        font-family: 'Iceberg', cursive;
        font-size: 1em;
        color: #523524;
    
    }

    .title__descriptors:hover {
        color: #54405B;
    
    }

    .icon-carousel {
        padding: 30px 100px;
        height: 185px;
    }

    .carousel-slides {
        margin-top: 1.5em;
        height: 150px;
    } 

    .carousel-images {
        height: 100px;
        width: auto;
    }
    
    .carousel-images:hover {
        height: 110px;

    }

    .slick-dots {
        display: none !important;
    }

    .about-me__img {
        height: 12em;
    }

    .about-me {
        height: fit-content;
    }

    .about-me__copy {
        font-size: small;
    
    }

}