@import url(https://fonts.googleapis.com/css2?family=Alice&family=Iceberg&family=Raleway:ital,wght@0,400;1,500&family=Roboto:ital,wght@0,300;0,400;0,500;0,700;0,900;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing:border-box;
}

.contact-page {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
}

.contact-form-intro {
  font-family: 'Alice', serif;
  font-size: 1.5em;
  color: #523524;
  margin: .75em 0;
}

.required {
  font-family: 'Iceberg', cursive;
  font-size: 1em;
  padding-top: 1em;
  display: block;
  text-align: center;
}

.form-container {
  width: 100vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-top: 3em;
  background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #c4d8d6;
  align-items: center;
  justify-content: center;
}

#contact-form {
  margin-left: auto;
  margin-right: auto;
  padding: 2em;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: solid 1pt #523524;
  border-radius: 5px;
  text-align: left;
  margin-bottom: 5em;
  display: block;
  /* background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
   */
  /* background-color:#c4d8d6; */
}

fieldset {
  border: none;
}

label {
  font-family: 'Iceberg', cursive;
  padding: .5em;
  display: block;
  color: #523524;
}

#inputFirstName, #inputLastName {
  padding-left: 1em;
  height: 2em;
  width: 15em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputEmail {
  padding-left: 1em;
  height: 2em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#inputMessage {
  padding-left: 1em;
  text-justify: initial;
  resize: none;
  height: 15em;
  width: 30em;
  border-radius: 5px;
  border: .5px #54405B solid;
}

#fs-button {
  text-align: center;
}

.con-response {
  font-family: 'Iceberg', cursive;
  font-size: 1.1em;
  color: #c7742b;
}

button {
  padding: .5em 1.5em;
  font-family: 'Iceberg', cursive;
  font-size: 1.25em;
  color: #eff4f7;
  background-color: #523524;
  border-radius: 10px;
}

.content-section {
  margin-bottom: 50px;
  flex: 1 0 auto;
}

.Footer {
  margin-top: -70px;
  height: 70px;
  background-color: #EFF4F7;
  display: flex;
  flex-shrink: 0;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.footer-text {
  font-family: 'Alice', serif;
  font-size: small;

}


@media only screen and (max-width: 650px) {

  .contact-form-intro {
    font-size: .8em;
  }

  #contact-form {
    padding: 1em;
  }

  #inputEmail,
  #inputMessage {
    width: 20em;
  }

  #inputMessage {
    height: 8em;
  }

  button {
    font-size: .8em;
    padding: .3em .8em;
  }

  .con-response {
    font-size: .75em;
  }

}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 3em;
}

.header__poem {
    display: flex;
    max-height: 1em;
    justify-content: start;
    align-items: flex-start;
}

.poem-text {
    padding-top: .5em;
    padding-left: 3em;
    font-family: 'Alice', serif;
    font-size: 1.25em;
    color: #523524;
}

.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 13rem;
    max-height: 13rem;
    min-height: 13rem;
}

.header__logo {
    height: 12rem;

}

.header__logo:active {
    height: 13rem;
}

.header__social-media {
    display: flex;
    flex-direction: row-reverse;
    max-height: 1em;
}

.header__smicon{
    align-self: end;
    justify-self: end;
    padding-top: .5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    padding-bottom: 0;
}

.header__smicon:hover {
    transform: translateY(3px);
}


@media only screen and (max-width: 650px) {
    .header {
        justify-content: center;
        align-items: center;
    }

    .header__poem {
        display: none;
    }

    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 6rem;
        min-height: 6rem;
        max-height: 6rem;
    }

    .header__logo {
        height: 6rem;
        max-height: 6rem;
        min-height: 6rem;
    }

    .header__social-media {
        flex-direction: row;
        margin: 0;
        max-height: .5em;
    }
    
    .header__smicon{
        padding-top: .5em;
        padding-left: .5em;
        padding-right: .5em;
        padding-bottom: 0;
        height: 25px;
        width: auto;
    }


    
}
.navbar {
    margin-top: 1rem;
    height: 3.5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #523524;
}


.navbar__link {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    text-decoration: none;
    font-size: 1.25rem;
}

.navbar__link:hover {
    transform: translateY(3px);
    font-size: 1.35em;
}

@media only screen and (max-width: 480px) {
    .navbar {
        height: 2.5em;
    }
    
    .navbar__link {
        margin: 0 .25em;
        font-size: .8em;
    }
}

/* project details popup styling */

.content__label-container {
    background-color: #523524;
    padding: 2em;
    width: 80vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.content__key {
    font-family: 'Iceberg', cursive;
    color: #EFF4F7;
    font-size: 1.25em;
    letter-spacing: 2px;
    margin: 0em 1.5em;
}


.content__value {
    font-family: 'Alice', serif;
    color: #EFF4F7;
    font-size: 1.15em;
}

.content__main-image {
    width: 450px;
    height: auto;
    border: #C7742B 25px solid;
    border-radius: 10px;
}

.content__prototype,
.content__myProcess {
    width: 600px;
    height: auto;
    border-radius: 10px;
}

.content__userJourney {
    width: 750px;
    height: auto;
    border: 1px solid #EFF4F7 ;
    border-radius: 10px;
}

.content__wireframes,
.content__userPersonas {
    width: 600px;
    height: auto;
    display: block;
    border-radius: 10px;
}

.content__iteration {
    width: 500px;
    height: auto;
    display: block;
    border-radius: 10px;
}

figcaption {
    font-family: 'Alice', serif;
    color: #C4D8D6;
    font-size: .85em;
    font-style: italic;
    margin-bottom: 1em;

}

.content__video{
    width: 650px;
    height: 365px;
}


@media only screen and (max-width: 650px) {
    .content__key {
        font-family: 'Iceberg', cursive;
        color: #EFF4F7;
        font-size: .85em;
        letter-spacing: 1px;
        margin: 0em 1.5em;
    }
    
    
    .content__value {
        font-family: 'Alice', serif;
        color: #EFF4F7;
        font-size: .85em;
    }
    
    .content__main-image {
        width: 90vw;
        border: #C7742B 15px solid;
    }
    
    .content__prototype,
    .content__myProcess {
        width: 90vw;

    }
    
    .content__userJourney {
        width: 95vw;

    }
    
    .content__wireframes,
    .content__userPersonas {
        width: 90vw;

    }
    
    .content__iteration {
        width: 90vw;
        height: auto;

    }
    
    .content__video{
        width: 90vw;
        height: auto;
    }
    

}
.content {
    margin-top: 4em;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.content__image {
    height: 15rem;
    width: auto;
    margin: auto;
}

.content__paragraph {
    color: #424c55;
    font-size: 1.2em;
    margin: .5em 25em;
}

.content__home {
    text-align: center;
    color: #424c55;
    font-size: 1.2em;
    margin: auto;
}

.content__additional {
    text-align: center;
    color: rgb(185, 63, 63);
    font-size: 1.2em;
}

.image__additional {
    height: 15rem;
    width: auto;
    margin: auto;
}



p {
    margin: 2em 0;
    text-align: center;
    color: #523524;
    font-size: 1.2rem;
    align-self: center;
}

.title {
    display: flex;
    height: 8em;
    justify-content: center;
    align-items: center;

}

.title__descriptors {
    font-family: 'Iceberg', cursive;
    font-size: 2em;
    color: #523524;

}

.title__descriptors:hover {
    transform: scale(1.5);
    color: #54405B;

}

.material-icons {
    color: #C7742B;
    padding: 0 5px;
}

.material-icons:hover {
    transform: scale(2);
    color: #54405B;
}

.icon-carousel {
    height: 24rem;
    background-color: #C4D8D6;
    padding: 50px 85px;
    text-decoration: none;
    border: none;
}

.carousel-slides {
    margin-top: 2em;
    height: 12.5rem;
    text-decoration: none;
    border: none;
} 

.carousel-images {
    height: 150px;
    width: auto;
    text-decoration: none;
    border: none;
}

.carousel-images:hover {
    height: 160px;
    text-decoration: none;
    border: none;
}

.about-me__container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-me__img {
    height: 30em;
    width: auto;
}

.about-me {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 30em;
    background-image: url('https://raw.githubusercontent.com/buzzfair/cultcodeSmr2020Cohort/master/images/logo-trans_500x500.png');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}


.about-me__copy {
    font-family: 'Alice', serif;
    font-size: 1.5em;
    color: #523524;
    margin: 1em;
    text-shadow: 2px 2px 2px #FFFFFF;
}

@media only screen and (max-width: 650px) {
    .title {
        height: 2em;
        margin: 1em;
    }

    .title__descriptors {
        font-family: 'Iceberg', cursive;
        font-size: 1em;
        color: #523524;
    
    }

    .title__descriptors:hover {
        color: #54405B;
    
    }

    .icon-carousel {
        padding: 30px 100px;
        height: 185px;
    }

    .carousel-slides {
        margin-top: 1.5em;
        height: 150px;
    } 

    .carousel-images {
        height: 100px;
        width: auto;
    }
    
    .carousel-images:hover {
        height: 110px;

    }

    .slick-dots {
        display: none !important;
    }

    .about-me__img {
        height: 12em;
    }

    .about-me {
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }

    .about-me__copy {
        font-size: small;
    
    }

}
p {
    margin-top: 2em;
    text-align: center;
    color: #424c55;
    font-size: 1.2rem;
    align-self: center;
}


.projects {
    display: flex;
    flex-direction: column;
    width: 100vw;
    justify-content: center;
    align-items: center;
}

.projects__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80vw;
    margin-bottom: 3em;
}


.projects__nav {
    margin: 2em 0;
    display: flex;
}


.projects__image {
    height: 23rem;
    width: auto;
    margin: 1em 2em;
    border: .5px #C7742B solid;
    transition: .5s ease;
    opacity: 1;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;

}

.projects__image-middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 75%;
    left: 25%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.projects__nav:hover .projects__image {
    opacity: 0.3;
}

.projects__nav:hover .projects__image-middle {
    opacity: 1;
}

.projects__image-middle-text {
    color: #523524;
    font-family: 'Alice', serif;
    padding: 16px 32px;
}

.projects__copy {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 2em 0;
}

.projects__name {
    color: #523524;
    font-family: 'Iceberg', cursive;
    font-size: 2em;
}

.projects__desc {
    color: #523524;
    font-family: 'Alice', serif;
    margin: 0 1em;
    text-align: justify;
}

button {
    font-size: 1em;
    margin-top: 1.5em;
    width: 7.5em;
    height: 3.75em;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}

/* inside popups */

.content__label-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.content-projects {
    position: relative;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.content__key,
.content__value {
    margin: 1em;
}

.popup-content {
    background-color: #523524 !important;
    position: absolute !important;
    border-radius: 20px !important;
    width: 82% !important;
    padding: 2em !important;
    height: 85% !important;
    left: 7% !important;
    right: 50px !important;
    overflow: scroll !important;
    text-align: center !important;
  }

  .close,
  .close:hover,
  .close:active {
    
    cursor: pointer;
    font-family: inherit;
    color:#EFF4F7;
    font-size: 2em;
    font-weight: 800;
  }



@media only screen and (max-width: 650px) {
    .projects__wrapper {
        flex-direction: column;
    }

    .projects__nav {
        margin: .75em 0;
    }

    .projects__image {
        height: 8rem;
        width: auto;
        margin: .5em 1em;
    }

    .projects__copy {
        padding: 1em 0;
    }
    
    .projects__name {
        font-size: .75em;
    }
    
    .projects__desc {
        text-align: left;
        font-size: smaller;
    }
    
    button {
        font-size: .6em;
        margin-top: .85em;
        width: 4.25em;
        height: 3em;
        padding: .25em;
    }

    
    .content__key,
    .content__value {
        margin: .75em;
        font-size: .75em;     
    }
    
    .popup-content {
        background-color: #523524 !important;
        position: absolute !important;
        border-radius: 20px !important;
        width: auto !important;
        padding: 1em !important;
        height: 50% !important;
        left: 6vw !important;
        right: 6vw !important;
        overflow: scroll !important;
        text-align: center !important;
      }
    
      .close,
      .close:hover,
      .close:active {
        
        cursor: pointer;
        font-family: inherit;
        color:#EFF4F7;
        font-size: 2em;
        font-weight: 800;
      }

}

