.header {
    display: flex;
    justify-content: center;
    align-items: center;
    max-height: 3em;
}

.header__poem {
    display: flex;
    max-height: 1em;
    justify-content: start;
    align-items: flex-start;
}

.poem-text {
    padding-top: .5em;
    padding-left: 3em;
    font-family: 'Alice', serif;
    font-size: 1.25em;
    color: #523524;
}

.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 13rem;
    max-height: 13rem;
    min-height: 13rem;
}

.header__logo {
    height: 12rem;

}

.header__logo:active {
    height: 13rem;
}

.header__social-media {
    display: flex;
    flex-direction: row-reverse;
    max-height: 1em;
}

.header__smicon{
    align-self: end;
    justify-self: end;
    padding-top: .5em;
    padding-left: 1.5em;
    padding-right: 1.5em;
    padding-bottom: 0;
}

.header__smicon:hover {
    transform: translateY(3px);
}


@media only screen and (max-width: 650px) {
    .header {
        justify-content: center;
        align-items: center;
    }

    .header__poem {
        display: none;
    }

    .logo{
        display: flex;
        justify-content: center;
        align-items: center;
        height: 6rem;
        min-height: 6rem;
        max-height: 6rem;
    }

    .header__logo {
        height: 6rem;
        max-height: 6rem;
        min-height: 6rem;
    }

    .header__social-media {
        flex-direction: row;
        margin: 0;
        max-height: .5em;
    }
    
    .header__smicon{
        padding-top: .5em;
        padding-left: .5em;
        padding-right: .5em;
        padding-bottom: 0;
        height: 25px;
        width: auto;
    }


    
}